@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;1,900&display=swap');
:root{
  --blue: #003f5c;
  --violet: #58508d;
  --magenta: #bc5090;
  --red: #ff6361;
  --orange: #ffa600;

  font-family: 'Lato', sans-serif;
}
.current, .other-links{
  margin: 0 2.5px 0 2.5px;
}
a:link, a:visited{
  text-decoration: none;
  color: black;
}
#navbar-links .current {
  color: var(--magenta);
}
#navbar-links .other-links, #header-bar a{
  color: var(--orange);
}
#navbar-links{
  background: var(--violet);
  color: var(--orange);
  padding: 0 0 0.5%  0.5%;
}
#main-content{
  display: flex;
  flex-wrap: wrap;
  margin-top: 1%;
}
#content{
  margin: 0 1% 0 1%;
}
.course{
  display: inline-block;
  max-width: 22%;
  margin: 0 1.5% 3% 1.5%;
  text-align: center;
}
.course p{
  align-items: center;
}

body{
  margin: 0;
  padding: 0;
}
#header-bar{
font-weight: bolder;
  padding: 1% 0 1.5% 0;
  background: var(--violet);
  color: var(--orange);
  text-align: center;
  font-size: 1.5rem;
}
#sections ul{
  list-style-type: none;
}
#sections li{
  font-size: 1.2rem;
}
@media only screen and (max-width: 820px){
  #main-content{
    flex-direction: row;
    align-items: center;
  }
  .course{
    max-width: 45%;
    margin: 0 auto;
  }
  h1{
    font-size: 2.5rem;
  }
  #sections li{
    font-size: 1.5rem;
    margin: 15px 0;
  }
  #header-bar{
    margin:0;
    font-size: 2.5rem;
  }
  #navbar-links{
    margin: 0;
    font-size: 1.5rem;
    padding:1% 0;
    text-align: center;
  }
  #content{
    padding-top: 1%;
  }
}